<script>
	import Button from './UI/Button.svelte';
	import TextInput from './UI/TextInput.svelte';
    import MeetupGrid from './Meetups/MeetupGrid.svelte';
    import Header from './UI/Header.svelte';

    let title = '';
    let subtitle = '';
    let address = '';
    let email = '';
    let description = '';
    let imageUrl = '';

    let meetups = [
        {
            id: 'm1',
            title: 'Coding Bootcamp',
            subtitle: 'LEarn to code in 2 hours',
            description: 'in this meetup, we will teach you how to write svelte code',
            imageUrl: 'data:image/jpeg;base64,',
            address: '5924 e king pl',
            contactEmail: 'code@test.com'
        },
        {
            id: 'm2',
            title: 'Swim Together',
            subtitle: 'lets swim together',
            description: 'in this meetup, we will teach you how to write svelte code',
            imageUrl: 'data:image/jpeg;base64,',
            address: '5928 e king pl',
            contactEmail: 'swimming@test.com'
        },
    ]
    function addMeetup(){
        const newMeetup = {
            id: Math.random().toString(),
            title: title,
            subtitle: subtitle,
            description: description,
            imageUrl: imageUrl,
            contactEmail: email,
            address: address
        }
        meetups = [...meetups, newMeetup]
    }
</script>

<Header/>

 <main>
     <form on:submit|preventDefault='{addMeetup}'>
         <TextInput 
         id='title' 
         label='title' 
         controlType='text' 
         value={title} 
         on:input='{(e) => (title = e.target.value)}'
         />

         <TextInput 
         id='subtitle' 
         label='subtitle' 
         controlType='text' 
         value={subtitle} on:input='{(e) => subtitle = e.target.value}'
         />

         <TextInput 
         id='imageUrl' 
         label='imageUrl' 
         controlType='text' 
         value={imageUrl} 
         on:input='{(e) => imageUrl = e.target.value}'
         />

         <TextInput 
         id='address' 
         label='address' 
         controlType='text' 
         value={address} 
         on:input='{(e) => address = e.target.value}'
         />

         <TextInput 
         type='email'
         id='email' 
         label='email' 
         controlType='email' 
         value={email} 
         on:input='{(e) => email = e.target.value}'
         />

         <TextInput 
         type='textarea'
         id='description' 
         label='description' 
         controlType='textarea' 
         value={description} 
         on:input='{(e) => description = e.target.value}'
         />

         <Button type='submit' caption='Submit'/>
     </form>
     <MeetupGrid {meetups}/>
 </main>
 <style>
     form{
         width: 30rem;
         margin:auto;
     }
 </style>